(function() {
  var FindProjectsCtrl,
    bind = function(fn, me){ return function(){ return fn.apply(me, arguments); }; };

  FindProjectsCtrl = (function() {
    function FindProjectsCtrl($state, $http, $scope, $modal) {
      var newSkills, ref, ref1, ref2, ref3, ref4;
      this.$state = $state;
      this.$http = $http;
      this.$scope = $scope;
      this.$modal = $modal;
      this.addMarker = bind(this.addMarker, this);
      this.showProjectMessage = false;
      this.showProjectsList = false;
      this.mapLocation = false;
      this.lastInfoWindow = false;
      this.skills = [];
      this.availableSkills = ['JavaScript', 'Java', 'Python', 'CSS', 'PHP', 'Ruby', 'C++', 'C'];
      newSkills = ['Shell', 'C#', 'Objective-C', 'R', 'VimL', 'Go', 'Perl', 'CoffeeScript'];
      (ref = this.availableSkills).push.apply(ref, newSkills);
      newSkills = ['TeX', 'Swift', 'Scala', 'Emacs Lisp', 'Haskell', 'Lua', 'Clojure', 'Matlab', 'Arduino', 'Groovy'];
      (ref1 = this.availableSkills).push.apply(ref1, newSkills);
      newSkills = ['Puppet', 'Rust', 'PowerShell', 'Erlang', 'Visual Basic', 'Processing', 'Assembly', 'TypeScript', 'XSLT'];
      (ref2 = this.availableSkills).push.apply(ref2, newSkills);
      newSkills = ['ActionScript', 'ASP', 'OCaml', 'D', 'Scheme', 'Dart', 'Common Lisp', 'Julia', 'F#', 'Elixir'];
      (ref3 = this.availableSkills).push.apply(ref3, newSkills);
      newSkills = ['FORTRAN', 'Haxe', 'Racket', 'Logos'];
      (ref4 = this.availableSkills).push.apply(ref4, newSkills);
      this.projects = [];
      this.$http.get('app/data/projects.json').success((function(_this) {
        return function(data) {
          var i, len, project, ref5, results;
          _this.projects = data;
          ref5 = _this.projects;
          results = [];
          for (i = 0, len = ref5.length; i < len; i++) {
            project = ref5[i];
            results.push(_this.addMarker(project));
          }
          return results;
        };
      })(this), function(error) {
        return console.log(error);
      });
      'ngInject';
    }

    FindProjectsCtrl.prototype.refreshResults = function($select) {
      var FLAG, list, search, userInputItem;
      search = $select.search;
      list = angular.copy($select.items);
      FLAG = -1;
      list = list.filter(function(item) {
        return item.id !== FLAG;
      });
      if (!search) {
        $select.items = list;
      } else {
        userInputItem = search;
        if (list.filter(function(elem) {
          return elem === search;
        }).length === 0) {
          $select.items = [userInputItem].concat(list);
        }
      }
    };

    FindProjectsCtrl.prototype.updateMap = function() {
      if ((typeof this.mapLocation) === 'object') {
        google.maps.event.trigger(this.$scope.map, 'resize');
        return this.$scope.map.setCenter(this.mapLocation.geometry.location);
      }
    };

    FindProjectsCtrl.prototype.showMap = function() {
      return (typeof this.mapLocation) === 'object';
    };

    FindProjectsCtrl.prototype.addMarker = function(project) {
      var contentString, infowindow, newMarker;
      if (!(project == null)) {
        contentString = '<div class="map-project">' + '<h4 class="map-project-title">' + project.name + '</h4>' + '<h5 class="map-project-title">' + project.author.name + '</h5>' + '<div class="map-project-description">' + project.description + '</div>' + '</div>';
        infowindow = new google.maps.InfoWindow({
          content: contentString
        });
        newMarker = new MarkerWithLabel({
          position: {
            lat: project.latitude,
            lng: project.longitude
          },
          draggable: false,
          icon: ' ',
          map: this.$scope.map,
          labelContent: '<i class="fa fa-desktop fa-2x" style="color:#E84A5F;"></i>',
          labelAnchor: new google.maps.Point(22, 50),
          labelClass: 'labels'
        });
      }
      newMarker.addListener('click', (function(_this) {
        return function() {
          if (_this.lastInfoWindow) {
            _this.lastInfoWindow.close();
          }
          _this.lastInfoWindow = infowindow;
          return infowindow.open(_this.$scope.map, newMarker);
        };
      })(this));
      newMarker.addListener('click', (function(_this) {
        return function() {
          return infowindow.open(_this.$scope.map, newMarker);
        };
      })(this));
      return google.maps.event.addListener(infowindow, 'domready', (function(_this) {
        return function() {
          return $('.dev-me-map-btn').click(function() {
            return _this.devMe();
          });
        };
      })(this));
    };

    FindProjectsCtrl.prototype.showProjectsList = function() {
      return this.showProjectsList;
    };

    FindProjectsCtrl.prototype.listProjects = function() {
      return this.showProjectsList = true;
    };

    FindProjectsCtrl.prototype.getProjects = function() {
      return this.projects;
    };

    FindProjectsCtrl.prototype.devMe = function() {
      this.showDeveloperMessage = true;
      return this.$modal.open({
        animation: true,
        templateUrl: 'app/message/message.html',
        size: 'lg',
        controller: 'MessageController',
        controllerAs: 'MessageCtrl'
      });
    };

    FindProjectsCtrl.prototype.clickBack = function() {
      return location.reload();
    };

    return FindProjectsCtrl;

  })();

  angular.module('devme').controller('FindProjectsController', ['$state', '$http', '$scope', '$modal', FindProjectsCtrl]);

}).call(this);



